import Vue from 'vue'
import App from './App.vue'
import VueScrollReveal from 'vue-scroll-reveal';
 
Vue.use(VueScrollReveal, {
  class: 'v-scroll-reveal', 
  duration: 1500,
  distance: '60px',
  delay: 200,
  origin: 'top'
});

Vue.config.productionTip = false

new Vue({
  render: h => h(App)
}).$mount('#app')
